<template>
  <div class="root">
      <div class="panel">
      <div class="sensor">
        Дом: {{ inTemp }} °C
      </div>
      <div class="sensor">
        Улица: {{ outTemp }} °C
      </div>
      <div class="sensor">
        Давление: {{ press }} мм.рт.ст
      </div>
    </div>
  </div>  
</template>

<script>
  export default {
    data(){
      return {
        inTemp: null,
        outTemp: null,
        press: null,
        ws: null,
      }
    },

    methods:{
      connect(){
      this.ws = new WebSocket('wss://peredaifile.ru:3031');

        this.ws.onmessage =  (event) => {
          let message = JSON.parse(event.data);
          console.log("сообщение от сервера!");
            if (message.type == 'sensors') {

              this.inTemp = message.value.inTemp;
              this.outTemp = message.value.outTemp;
              this.press = message.value.press;

            }
          }   
        }
      },

    mounted(){
    this.connect();
    
    
  }
    
  }
</script>


